@charset "UTF-8";
/* main.css */
*,
*::after,
*::before,
p,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

a {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
  text-decoration: none;
}

:root {
  --main-text: #0E0E0E;
  --muted-text: #7D7D7D;
  --white-text: #ffffff;
  --black: #0E0E0E;
  --gray-bg: #F5F5F7;
  --gray-hover-bg: #E1E1E3;
  --black-bg: #0E0E0E;
  --blue: #5AA0F5;
}

body {
  font-family: "Inter", sans-serif;
  color: var(--main-text);
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
}

.container {
  max-width: 1514px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.header {
  background-color: var(--black);
  color: var(--white-text);
  padding: 19px 0;
  margin-bottom: 40px;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn__back_link {
  display: flex;
  gap: 6px;
  align-items: center;
}
.btn__back_link.btn__back_link-mobile {
  display: none;
  font-size: 0;
}

.header__logo {
  font-size: 0;
}

.btn__link {
  padding: 0 28.5px;
  border: 2px solid #5B5B6A;
  border-radius: 8px;
  line-height: 48px;
  text-align: center;
}
.btn__link.btn__link-mobile {
  display: none;
  border: none;
  padding: 0;
  font-size: 0;
  line-height: 1;
}

.box {
  width: 100%;
  height: 200px;
  background-color: red;
}
.box.green {
  background-color: green;
}

.сheckout__title {
  font-weight: bold;
  font-size: 36px;
  line-height: 1.22;
  margin-bottom: 24px;
}
.сheckout__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.сheckout__desc {
  width: 58.3333333333%;
  padding: 20px 32px;
  border: 1px solid #E1E1E3;
  border-radius: 12px;
}
.сheckout__order_details {
  width: 440px;
}
.сheckout__account {
  display: flex;
  justify-content: space-between;
}
.сheckout__account_city {
  display: inline;
}
.сheckout__account_city-link {
  color: #2A65C5;
}
.сheckout__account_city-link svg {
  vertical-align: bottom;
}
.сheckout__account_type input[type=radio] {
  display: none;
}
.сheckout__account_type label {
  display: inline-block;
  cursor: pointer;
  position: relative;
  padding-left: 25px;
  margin-right: 0;
  line-height: 18px;
  user-select: none;
}
.сheckout__account_type label::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
  bottom: 1px;
  border: 1px solid #d7d7d7;
  border-radius: 20px;
}

