/* adative*/
@media (max-width: 1920px) {
  .container {
    width: 1346px;
    padding: 0;
  }
}
@media (max-width: 1440px) {
  .container {
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 12px;
  }
  .header {
    padding: 21.5px 0;
  }
  .btn__back_link {
    display: none;
  }
  .btn__back_link.btn__back_link-mobile {
    display: block;
  }
  .header__logo img {
    height: 42px;
  }
  .btn__link {
    display: none;
  }
  .btn__link.btn__link-mobile {
    display: block;
  }
}
@media (max-width: 375px) {
  .header {
    padding: 12px 0;
  }
  .header__logo img {
    height: 23px;
  }
  .header__wrapper {
    margin: 0 8px;
  }
}